@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');

:root {
    --color-primary: #262A2F;

    --container-width: 800px;
    /*--grid-column-width: 98px;*/
    /*--grid-gap: 20px;*/
}

* {
    all: unset;
    display: revert;
}

html {
    /*background-image: url("./image/sunny.jpg");*/
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
}

body {
    font-family: 'Nunito', sans-serif;
}

.app {
    width: 100%;
    height: 100vh;
    position: relative;
    background-color: rgba(0,0,0,0.4);
    color: #fff;
}

.app:before {
    content: '';
    background: url('./image/bg.jpg') no-repeat center center/cover;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
}

.container {
    max-width: var(--container-width);
    margin: 0 auto;
    padding: 0 1rem;
    /*height: 100%;*/
    position: relative;
    top: 10%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.search {
    text-align: center;
    padding: 1rem;
}

.app input {
    padding: .7rem 1.5rem;
    font-size: 1.2rem;
    border-radius: 20px;
    border: 1px solid rgba(255,255,255, 0.8);
    background: rgba(255,255,255, 0.1);
}

::placeholder {
    color: #f8f8f8;
}

.top {
    width: 100%;
    margin: 1rem auto;
    /*flex: 1 0 auto;*/
}

.description {
    position: relative;
    right: -90%;
    transform-origin: 0 0;
    transform: rotate(269deg);
}

.bottom {
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    width: 100%;
    margin: 1rem auto;
    padding: 1rem;
    border-radius: 10px;
    background-color: rgba(255,255,255, 0.2);
    /*flex: 0 0 auto;*/
}

p {
    font-size: 1.6rem;
}

h1 {
    font-size: 6rem;
}

.bold {
    font-weight: 700;
}